@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&family=STIX+Two+Math&display=swap');

$color-red: #ff0000;

body {
  color: #333;
  font-family: 'STIX Two Math','Noto Sans KR', sans-serif;

  a {
    text-decoration: none;
  }

  h1 {
    font-size: 5rem;
  }

  h2 {
    font-size: 3rem;
  }

  h3 {
    font-size: 2rem;
  }

  h4 {
    font-size: 1.8rem;
  }

  h5 {
    font-size: 1.5rem;
    line-height: 1.8rem;
  }

  h6 {
    font-size: 1.2rem;
  }

  .container {
    width: 1200px;
    margin: 0 auto;
  }

  .title {
    position: relative;
    margin-bottom: 3rem;

    i {
      font-size: 3rem;
      margin-right: 1rem;
      color: $color-red;
    }

    h3 {
      font-weight: 800;
      display: inline;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  // HEADER
  header {
    position: relative;

    .container {
      display: flex;
      justify-content: space-between;
    }

    .logo {
      width: 260px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      img {
        width: 60px;
      }

      h1 {
        font-size: 2rem;
        color: $color-red;
      }
    }

    .main-nav {
      position: absolute;
      bottom: 0;
      right: 20%;

      ul {
        width: 600px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        li {
          border-top: 1px solid $color-red;
          border-left: 1px solid $color-red;
          border-right: 1px solid $color-red;
          padding: 1rem 2rem;
          border-radius: 20px 20px 0 0;

          a {
            transition: 0.5s;
            color: $color-red;

            &:hover {
              color: #000;
            }
          }
        }
      }
    }
  }

  // MAIN
  main {
    padding: 5rem 0;
    text-align: center;
    border-top: 1px solid $color-red;
    border-radius: 20px;
    border-bottom: 1px solid $color-red;
    margin: 0 3rem;

    h2 {
      font-size: 5rem;
      font-weight: 800;
    }

    .bar {
      width: 100px;
      height: 5px;
      margin: 3rem auto;
      color: $color-red;
      background-color: $color-red;
      border-radius: 30px;
    }
  }

  // ABOUT ME
  .about {
    position: relative;
    margin: 5rem auto;

    .about-section {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      gap: 2rem;

      * {
        display: flex;
        justify-content: start;
        align-items: center;

        i {
          font-size: 2rem;
          margin-right: 1rem;
          color: $color-red;
        }
      }
    }
  }

  // SKILLS
  .skills {
    margin: 0 auto;

    .s-box {
      h5 {
        padding-bottom: 0.5rem;
        border-bottom: 2px solid #D3D3D3;
      }

      div {
        background-color: #D3D3D3;
        margin: 1rem;
        padding: 1rem;
        color: $color-red;
      }

      .s-80 {
        width: 80%;
      }

      .s-60 {
        width: 60%;
      }

      .s-50 {
        width: 50%;
      }

      .s-30 {
        width: 30%;
      }
    }
  }

  // PROJECTS
  .projects {
    margin: 5rem auto;

    .p-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 5rem auto;

      img {
        width: 600px;
        border: 1px solid $color-red;
        border-radius: 20px;
        padding: 2rem;
        transition: 0.8s;

        &:hover {
          transform: translateY(-50px);
        }
      }

      .p-txt {
        width: 500px;

        .p-btn-box {
          margin: 2rem 0;

          .btn {
            padding: 0.5rem 2rem;
            background: none;
            border: 1px solid $color-red;
            border-radius: 20px;
            cursor: pointer;
            transition: 0.8s;

            &:hover {
              background: $color-red;
            }

            &:hover a {
              color: #fff;
            }
            a {
              color: #333;
            }
          }
        }
      }
    }

    h6 {
      margin-bottom: 2rem;
    }

    .programs {
      img {
        width: 200px;
        border: none;
        border-radius: 0;
        padding: 0rem;
        &:hover {
          transform: translateY(0px);
        }
      }
    }
  }

  // RULES
  .rules {
    .rules-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 3rem;
      >* {
        text-align: center;
        border: 1px solid $color-red;
        border-radius: 30px;
        padding: 3rem;
        p {
          line-height: 1.3rem;
        }
      }
    }
  } 

  // FOOTER
  footer {
    text-align: center;
    position: relative;
    height: 300px;
    margin-top: 5rem;

    h3 {
      margin: 3rem 0;
    }

    .storage {
      ul {
        display: flex;
        justify-content: center;
        align-items: center;

        li {
          a {
            padding: 0.5rem;

            &:hover img {
              transform: translateY(-20px);
            }

            img {
              width: 50px;

              &:last-child {
                border-radius: 50%;
                transition: 0.8s;
              }
            }
          }
        }
      }
    }

    .contacts {
      margin: 3rem 0;
      display: flex;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);

      .c-box {
        display: flex;
        align-items: center;
        margin: 0 2rem;

        i {
          font-size: 2rem;
          margin-right: 1rem;
        }
      }
    }

    p {
      margin-top: 7rem;
    }
  }
}